/*
 * This is the main file for the whole project.
 * No style definitions should be made here, only `@import`
 */
/*
 * Mixins and functions
 */
:root {
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto;
  font-size: 16px;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto;
  font-size: 16px;
  line-height: normal;
  -webkit-tap-highlight-color: transparent; }

body {
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto;
  font-size: 16px;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  text-rendering: optimizeLegibility; }

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
  border: 0; }

textarea {
  height: auto;
  min-height: 50px;
  overflow: auto; }

select {
  width: 100%; }

button {
  padding: 0;
  overflow: visible;
  line-height: 1;
  background: transparent;
  border: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  text-decoration: none;
  cursor: pointer;
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

mark {
  color: #ffffff;
  background: #ffff00; }

small {
  font-size: 80%; }

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font: inherit;
  color: inherit; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  cursor: pointer;
  appearance: button; }

button[disabled],
html input[disabled] {
  cursor: not-allowed; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0; }

:focus {
  outline: none; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  box-sizing: content-box;
  appearance: textfield; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  appearance: none; }

legend {
  padding: 0;
  border: 0; }

optgroup {
  font-weight: bold; }

table {
  border-spacing: 0;
  border-collapse: collapse; }

td,
th {
  padding: 0; }

/*  Avatar Base
* ====================================== */
.c-avatar {
  font-size: 0.75rem;
  height: 1.5rem;
  width: 1.5rem;
  background: #1c7cd6;
  border-radius: 200px;
  color: white;
  display: inline-block;
  text-align: center;
  font-weight: 400;
  line-height: normal;
  position: relative;
  vertical-align: middle; }
  .c-avatar i {
    font-size: 0.75rem;
    line-height: 1.5rem; }
  .c-avatar-square {
    border-radius: 0; }
  .c-avatar img {
    position: relative;
    border-radius: 200px;
    height: 100%;
    width: 100%;
    z-index: 0; }
  .c-avatar[data-status]::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    min-height: .5em;
    min-width: .5em;
    background-clip: padding-box;
    border-radius: 200px;
    box-shadow: 0 0 0 1px white;
    color: white;
    z-index: 1; }
  .c-avatar[data-badge]::after {
    position: absolute;
    top: 14.64%;
    right: 14.64%;
    transform: translate(50%, -50%);
    background: #1c7cd6;
    border-radius: 200px;
    z-index: 1;
    content: attr(data-badge);
    box-shadow: 0 0 0 1px white;
    font-size: 0.75rem;
    height: 1rem;
    line-height: 1;
    min-width: 1rem;
    padding: 0.125rem 0.25rem;
    text-align: center;
    white-space: nowrap; }
  .c-avatar[data-text]::before {
    color: currentColor;
    content: attr(data-text);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 0; }

.c-avatar-gray {
  background: #495057; }

.c-avatar[data-status='gray']::after {
  background-color: #495057; }

.c-avatar-primary {
